/* Composant Slideshow */

.carousel-container {
    position: relative;
    top: 35px;
    max-width: 100%;
    height: 415px;
    align-items: center;
    margin: 20px 45px;
}
.image-slide {
    width: 100%;
    height: 415px;
    object-fit: cover;
    border-radius: 10px;
}
.right-arrow {
    position: absolute;
    padding: 20px 0px 20px 20px;
    top: 40%;
    right: 1px;
    width: 3.2%;
    color: white;
    cursor: pointer;
    z-index: 2;
}
.left-arrow {
    position: absolute;
    padding: 20px 20px 20px 0px;
    top: 38%;
    left: 1px;
    width: 3%;
    color: white;
    cursor: pointer;
    z-index: 2;
    user-select: none;
}
.right-arrow img {
    width: 95%;
}
.left-arrow img {
    width: 100%;
}
.chevrons-slide {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.counter {
    position: absolute;
    bottom: 10px;
    left: 50%;
    align-items: center;
    color: white;
    font-size: 1.2rem;
    z-index: 2;
    user-select: none;
}
.slide {
    opacity: 0.5;
    transition-duration: 1s ease;
}
.slide.active{
    opacity: 1;
    transition-duration: 1s;
    transform: scale(1.08);
}


@media screen and (max-width: 1200px) {

.carousel-container {
    margin-left: 27px;
    margin-right: 27px;
}
}

@media screen and (max-width: 1035px){

.right-arrow {
    width: 4.2%;
}
.left-arrow {
    width: 4%;
}
.right-arrow img {
    width: 95%;
}
.left-arrow img {
    width: 100%;
}
}

@media screen and (max-width: 779px){

.carousel-container {
    margin-left: 20px;
    margin-right: 20px;
}
.counter {
    display: none;
}
.right-arrow {
    width: 5.2%;
}
.left-arrow {
    width: 5%;
}
}

@media screen and (max-width: 550px){

.carousel-container {
    margin-left: 15px;
    margin-right: 15px;
    height: 300px;
}
.image-slide {
    height: 300px;
}
.right-arrow {
    width: 6%;
    top: 35%
}
.left-arrow {
    width: 6%;
    top: 35%;
}
}

@media screen and (max-width: 400px){

.carousel-container {
    margin-left: 10px;
    margin-right: 10px;
    height: 255px;
}
.image-slide {
    height: 260px;
}
}