/* Page Home */

.titre-h1 {
    margin-top: 50px;
    margin-bottom: 50px;
    background-image: linear-gradient(
        rgba(0, 0, 0, 0.3), 
        rgba(0, 0, 0, 0.3)
      ), url("../assets/image-background-h1.png");
    background-size: cover;
    background-position: center;
    padding: 70px;
    font-size: 50px;
    text-align: center;
    font-weight: 400;
    color: white;
    border-radius: 20px;
}
.span-h1 {
    color: white;
}


@media screen and (max-width: 1200px) {

.titre-h1 {
    font-size: 38px;
}
}

@media screen and (max-width: 779px){

.titre-h1 {
    position: relative;
    font-size: 30px;
    text-align: start;
    margin-top: 20px;
    margin-bottom: 40px;
    padding: 45px 70px 70px 50px;
    border-radius: 10px;
}
.span-h1 {
    position: absolute;
    left: 50px;
    top: 75px;
}
}

@media screen and (max-width: 550px){

.titre-h1 {
    padding-top: 40px;
    font-size: 30px;
    padding-left: 25px;
}
.span-h1 {
    top: 70px;
    left: 25px;
}
}

@media screen and (max-width: 400px){

.titre-h1 {
    font-size: 25px;
    padding-left: 20px;
}
.span-h1 {
    left: 20px;
}
}