/* Composant Logo */

.logo-container {
    width: 180px;
}
.logo {
    margin-left : 0;
    width: 100%;
}


@media screen and (max-width: 779px){

.logo-container {
    max-width: 160px;
}
}

@media screen and (max-width: 550px){
.logo-container {
    max-width: 140px;
}
}

@media screen and (max-width: 400px){
.logo-container {
    max-width: 120px;
}
}